import React from 'react';

import {
  Container,
  Row,
  Col,
  ListGroup,
  Spinner,
  Image
} from 'react-bootstrap';

const ArtistList = props => {
  if (props.artists.length === 0) {
    return (
      <div className="d-flex align-items-center justify-content-center">
        <Spinner animation="border" role="status">
          <span className="sr-only">Loading...</span>
        </Spinner>
      </div>
    );
  } else {
    return (
      <ListGroup variant="flush">
        {props.artists.map((artist, key) => {
          return (
            <ListGroup.Item>
              <Container>
                <Row>
                  <Col xs={1}>
                    <h4 className="font-weight-bold">{key + 1}</h4>
                  </Col>
                  <Col xs={2}>
                    <Image className="thumbnail" src={artist.images[2].url} />
                  </Col>
                  <Col xs={7}>
                    <h5 className="align-items-center">{artist.name}</h5>
                    <p style={{ fontSize: '0.25rem' }}>
                      {artist.genres.join(', ')}
                    </p>
                  </Col>
                  <Col className="align-self-center" xs={2}>
                    <h6>{artist.popularity}%</h6>
                    <p>Popularity</p>
                  </Col>
                </Row>
              </Container>
            </ListGroup.Item>
          );
        })}
      </ListGroup>
    );
  }
};

export default ArtistList;
